</template>
<script>
  export default {
    data:function(){
      return {
        opePlan:'ffffs能否你偶读，飞的鸟， 干扰可否额，工人安排， 该金融案件， 过几日宝',
        data:[]
      }
    },
    created:function(){
      this.getData();
    },
    methods:{
      getData:function(){
        var url = '/api/com/wap/getOperationManager';
        this.$http.post(url).then(function(res){
          console.log(res);
          if(res.body.zt === 'true'){
            this.data = res.body.data
          }
        });
      }
    }
  }
</script>
<style scoped>
#opePlanDiv{
  box-sizing: border-box;
  padding:6px;
}
.opePlanTitle{
  box-sizing: border-box;
  padding:6px;
}
</style>



