<!-- 以后项目的根组件 -->
<template>
	<div>

    <!-- 1.0 利用mint-ui的header组件实现整个系统的头部 -->
    <!--<mt-header id="header" fixed title="日间手术管理系统">
      <router-link to="" slot="left" id="goBack" v-show="isShow">
        <i class="mint-toast-icon mintui mintui-back" @click="goBackFn"></i>
      </router-link>
    </mt-header>-->
    <!--<header>-->
    <!--</header>-->


		<!-- 路由占位符 -->
		<router-view id="content"></router-view>




    <!-- 3.0 利用mui的tabbar组件实现整个系统的底部 -->
    <nav class="mui-bar mui-bar-tab">
      <router-link v-for="item in footerList" :key="item.id" class="mui-tab-item" :to="item.path">
        <img :src="item.src" alt=""/>
        <span class="mui-tab-label">{{ item.name }}</span>
      </router-link>
    </nav>
	</div>
</template>

<script>
// 按需导入
	import { Toast } from 'mint-ui';

	// 负责导出 .vue这个组件对象(它本质上是一个Vue对象,所以Vue中该定义的元素都可以使用)
	// function add(x,y){console.log(x+y)}
	// module.exports = {  //es5的导出对象的写法
	export default{  // es6的导出对象的写法
    name:'App',
		data:function(){  //等价于 es5的 data:function(){
			return {
        footerList:[
          {path:'/',src:'../static/imgs/iconIndex/iphone6sUp/ico_1_dh_n@3x.png',name:'首页'},
          {path:'/personalPage',src:'../static/imgs/iconIndex/iphone6sUp/ico_2_dh_n@3x.png',name:'我的'}
        ],
        isShow:false,
        indexImg:'../static/imgs/iconIndex/iphone6sUp/ico_1_dh_h@3x.png',
        indexImgN:'../static/imgs/iconIndex/iphone6sUp/ico_1_dh_n@3x.png',
        myImg:'../static/imgs/iconIndex/iphone6sUp/ico_2_dh_h@3x.png',
        myImgN:'../static/imgs/iconIndex/iphone6sUp/ico_2_dh_n@3x.png'
			}
		},
    watch:{
      $route:function(now,old){     //监控路由变换，控制返回按钮的显示
        if(now.path=="/index" || now.path=="/"){
          this.isShow=false;
        } else{
          this.isShow=true;
        }
      }
    },
		methods:{
      myClick:function(){
        console.log('231');
      },
      indexClick:function(){
        console.log('231');
        this.indexImg = '../static/imgs/iconIndex/iphone6/ico_1_dh_n@2x.png';
      },
      //全局返回按钮
      goBackFn:function(){
        this.$router.go(-1);//返回上一层
      },
			// substrict:substrict
			tip:function(){
				Toast('你好');
			}
		},
		created:function(){

		}
	}
</script>

<style scoped>
/*当前页面的css样式写到这里，其中scoped表示这个里面写的css代码只是在当前组件页面上有效，不会去影响到其他组件页面*/
	.red{
		color: red;
	}
  #content{
    /*margin-top: 60px;*/
    margin-bottom:60px;
  }
  #header{
    height:60px;
  }
  #goBack{
    width:60px;
    height:60px;
    position: fixed;
    left:0;
    top:0;
    line-height: 60px;
  }
  .mui-bar img{
    width:22px;
    margin-bottom:-6px;
  }
  .mui-bar span{
    font-size:10px;
    display: list-item;
  }
</style>



