<!-- 默认首页 -->

<template>
	<div>
      <!--使用mint-ui中的轮播组件-->
      <mt-swipe :style="bannerImg" class="mintUiSwipe" :auto="3000">
        <mt-swipe-item v-for="item in list" :key="item.id">
          <img :src="item.img">
        </mt-swipe-item>
      </mt-swipe>
      <!-- 使用mui中的9宫格样式 -->
      <div class="mui-content" style="border:0">
        <ul :style="ulHeight" class="mui-table-view mui-grid-view mui-grid-9" style="border:0">
          <li :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="/SurgicalArrangement">
              <img :style="imgsSize" :src="icon1"/>
              <div class="mui-media-body">手术排程</div>
            </router-link>
          </li>
          <li :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="/opePlan">
              <img :style="imgsSize" :src="icon2"/>
              <div class="mui-media-body">术日安排</div>
            </router-link>
          </li>
          <li :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="/opeEducation">
              <img :style="imgsSize" :src="icon3"/>
              <div class="mui-media-body">术前宣教</div>
            </router-link>
          </li>
          <li :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="/outHospitalAppraise">
              <img :style="imgsSize" :src="icon4"/>
              <div class="mui-media-body">离院评估</div>
            </router-link>
          </li>
          <li :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="/outHospitalEducation">
              <img :style="imgsSize" :src="icon5"/>
              <div class="mui-media-body">出院指导</div>
            </router-link>
          </li>
          <li :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="/returnVisit">
              <img :style="imgsSize" :src="icon6"/>
              <div class="mui-media-body">复诊</div>
            </router-link>
          </li>
          <li :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="">
              <img :style="imgsSize" :src="icon7"/>
              <div class="mui-media-body">术后随访</div>
            </router-link>
          </li>
          <li @click="clickMessage" :style="liHeight" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
            <router-link to="/LeavingMessage">
              <img :style="imgsSize" :src="icon8" style="position:relative"/>
              <span v-show="messageNum != 0" class="weui-badge" style="position: absolute;top: .6em;right: 1.6em;">{{ messageNum }}</span>
              <div class="mui-media-body">留言</div>
            </router-link>
          </li>
        </ul>
    </div>
	</div>
</template>

<script>
	export default{
      data:function(){
        return {
          //获得手机屏幕的宽度
          phoneWidth:'',
          messageNum:'',
          liHeight:{
            height:0
          },
          bannerImg:{
            height:0
          },
          ulHeight:{
            height:0
          },
          imgsSize:{
            width:0
          },
          list:[
            {img:'../../static/imgs/iconIndex/iphone6sUp/banner1@3x.png'},
            {img:'../../static/imgs/iconIndex/iphone6sUp/banner2@3x.png'},
            {img:'../../static/imgs/iconIndex/iphone6sUp/banner3@3x.png'}
          ],
          menuList:[
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_1_gn.png',menuName:'手术排程',menuUrl:'/SurgicalArrangement'},
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_2_gn@3x.png',menuName:'术日安排',menuUrl:'/opePlan'},
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_3_gn@3x.png',menuName:'术前宣教',menuUrl:'/opeEducation'},
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_4_gn@3x.png',menuName:'离院评估',menuUrl:'/outHospitalAppraise'},
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_5_gn@3x.png',menuName:'出院指导',menuUrl:'/outHospitalEducation'},
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_6_gn@3x.png',menuName:'复诊',menuUrl:'/returnVisit'},
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_7_gn@3x.png',menuName:'术后随访',menuUrl:''},
            {iconUrl:'../../static/imgs/iconIndex/iphone6sUp/ico_8_gn@3x.png',menuName:'留言',menuUrl:'/LeavingMessage'}
          ],
          icon1:'../../static/imgs/iconIndex/iphone6sUp/ico_1_gn@3x.png',
          icon2:'../../static/imgs/iconIndex/iphone6sUp/ico_2_gn@3x.png',
          icon3:'../../static/imgs/iconIndex/iphone6sUp/ico_3_gn@3x.png',
          icon4:'../../static/imgs/iconIndex/iphone6sUp/ico_4_gn@3x.png',
          icon5:'../../static/imgs/iconIndex/iphone6sUp/ico_5_gn@3x.png',
          icon6:'../../static/imgs/iconIndex/iphone6sUp/ico_6_gn@3x.png',
          icon7:'../../static/imgs/iconIndex/iphone6sUp/ico_7_gn@3x.png',
          icon8:'../../static/imgs/iconIndex/iphone6sUp/ico_8_gn@3x.png'
        }
      },
    created:function(){
      this.imgsSize.width = window.innerWidth*26/375+'px';
      this.ulHeight.height = window.innerWidth*136/375+'px';
      this.liHeight.height = (window.innerWidth*136/375)/2+'px';
      this.bannerImg.height = window.innerWidth*160/375+'px';
     // console.log(this.liHeight.height);
      this.getMessageNum();
    },
    methods:{
      //获取右上角的未读数据
      getMessageNum:function(){
        var url = '/api/com/wap/getReadCount';
        this.$http.post(url).then(function(res){
          //console.log(res);
          this.messageNum = res.body.count;
          //sessionStorage.setItem('count',res.body.count);
          //this.messageNum = sessionStorage.getItem('count');
        });
      },
      clickMessage:function(){
        //sessionStorage.clear('count');
        //this.messageNum = '';
        var url = '/api/com/wap/getReadCount';
        this.$http.post(url,{num:1},{emulateJSON:true}).then(function(res){
          //console.log(res);
          this.messageNum = res.body.count;
          //sessionStorage.setItem('count',res.body.count);
          //this.messageNum = sessionStorage.getItem('count');
        });
      }
    }
	}
</script>

<style scoped>
.mintUiSwipe>>>.mint-swipe-indicators{
  height:6px;
  bottom:16px;
}
.mintUiSwipe>>>.mint-swipe-indicator{
  background-color: rgba(150,150,150,0.4)!important;
  opacity: 1;
  height:6px;
  width:6px;
  margin:0 6px;
}
.mintUiSwipe>>>.mint-swipe-indicator.is-active{
  background-color: #5596f2!important;
  opacity: 1;
}
.mui-grid-view.mui-grid-9 .mui-table-view-cell{
  border:none;
}
.mui-grid-view.mui-grid-9{
  background-color: #fff;
}
.mui-content > .mui-table-view:first-child,
.mui-content{
  margin-top:0px;
}
.mui-table-view li{
  width:25%;
}
/*.mintUiSwipe{
  height:160px;
}*/
.mintUiSwipe img{
  width:100%;
 /* height:160px;*/
}
.mui-table-view.mui-grid-view .mui-table-view-cell .mui-media-body{
  font-size:12px;
}
li img{
  width:26px;
}
li a div{
  font-family: "PingFang SC";
  font-size: 12px;
}
.mui-grid-view.mui-grid-9 .mui-table-view-cell,
.mui-table-view-cell,
li{
  padding:0;
}
</style>



