<!-- 术前宣教 -->
<template>
  <div id="tmpl">
    <ul class="mui-table-view">
      <li v-for="item in FileList" class="mui-table-view-cell">
        <router-link :to="{path:'/opeEducationDetails',query:{fileId:item.fId}}" class="mui-navigate-right">
          <span class="imgParent">
            <img v-if="item.fileLx=='docx'" src="../../../static/imgs/opeEducationImgs/Fimg.jpg" alt=""/>
            <img v-else-if="item.fileLx=='excel'" src="../../../static/imgs/opeEducationImgs/Eimg.jpg" alt=""/>
            <img v-else-if="item.fileLx=='jpg'" src="../../../static/imgs/opeEducationImgs/Iimg.jpg" alt=""/>
            <img v-else-if="item.fileLx=='png'" src="../../../static/imgs/opeEducationImgs/Pimg.jpg" alt=""/>
            <img v-else-if="item.fileLx=='mp4'" src="../../../static/imgs/opeEducationImgs/Vimg.jpg" alt=""/>
            <img v-else-if="item.fileLx=='3gp'" src="../../../static/imgs/opeEducationImgs/Wimg.jpg" alt=""/>
            <img v-else src="" alt=""/>
          </span>
          <span v-show="false">{{ item.fId }}</span>
          <span v-show="false">{{item.fileLx}}</span>
          <span>{{ item.fileName }}</span>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        FileList:[],
        fileType:0
      }
    },
    created:function(){
      this.getFileList();
    },
    methods:{
      getFileList:function(){
        var url = '/api/com/wap/getFileList';
        this.$http.get(url,{params:{opeId:360,fileType:this.fileType}}).then(function(res){
          this.FileList = res.body.data;
          //console.log(this.FileList);
        });
      }
    }
  }
</script>

<style scoped>
  img{
    width:10%;
  }
/*ul{
  list-style: none;
}
li{
  height:30px;
  margin-bottom:4px;
}
li:first-child{
  height:30px;
  margin-bottom:4px;
  margin-top:4px;
}
li a{
  display: inline-block;
  width:100%;
  height:30px;
  line-height: 30px;
}
li a span:last-child{
  display: inline-block;
  width:60%;
  height:30px;
  line-height: 30px;
}
.imgParent{
  display: inline-block;
  width:30px;
  height:30px;
}
.imgParent img{
  width:100%;
}*/
</style>



