<!--底部‘我的’页面开发-->
<template>
  <div id="tmpl">
    <div class="weui-cells weui-cells1">
     <!-- <router-link  class="weui-cell weui-cell_access weui-cell1" :to="{path:'/personalInfoPage',query:{userName:userName}}">
        -->
      <router-link  class="weui-cell weui-cell_access weui-cell1" to="/personalInfoPage">
        <div class="weui-cell__hd">
          <img class="imgsStyle" :src="userInfo.headimgurl">
        </div>
        <div class="weui-cell__bd">
          <h4>{{ userInfo.nickname }}</h4>
        </div>
        <span v-show="userInfo.isQua == 0" class="weui-badge" style="border-color:red;color:red;margin-left: 5px;">未认证</span>
        <span v-show="userInfo.isQua == 1" class="weui-badge" style="margin-left: 5px;">已认证</span>
        <i class="mui-icon mui-icon-arrowright"></i>
      </router-link>
    </div>
    <div class="weui-cells">
      <router-link class="weui-cell weui-cell_access" to="">
        <div class="weui-cell__bd">
          <p>我的检查单</p>
        </div>
        <div class="weui-cell__ft">
        </div>
      </router-link>
      <router-link class="weui-cell weui-cell_access" to="">
        <div class="weui-cell__bd">
          <p>手术同意书</p>
        </div>
        <div class="weui-cell__ft">
        </div>
      </router-link>
      <router-link class="weui-cell weui-cell_access" to="/suggestion">
        <div class="weui-cell__bd">
          <p>意见反馈</p>
        </div>
        <div class="weui-cell__ft">
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
  export default {
    data:function(){
      return {
        userInfo:[],
        userName:''
      }
    },
    created:function(){
      this.getUserInfo();
    },
    methods:{
      //获得用户的基本信息
      getUserInfo:function(){
        var url = '/api/com/wap/getWapUserInfo';
        this.$http.post(url).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            this.userInfo = res.body.data;
            this.userName = res.body.data.nickname;
          }
        });
      }
    }
  }
</script>
<style scoped>
  .mui-icon-arrowright{
    color:#fff;
  }
  .imgsStyle{
    border-radius:50%;
    width:60px;
    margin-right:5px;
    display:block;
  }
  .weui-cells1{
    background-color: #FFC73E;
  }
  .weui-cell1{
    padding:30px 15px;
  }
  .weui-badge{
    border:1px solid #3FC46B;
    background-color: #fff;
    color:#3FC46B;
    margin-left: 5px;
  }
  p{
    margin-bottom:0;
  }
</style>



