<!--留言组件系统页面-->
<template>
  <div id="tmpl">
    <!--<mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">-->
    <div class="item">
      <div class="name" style="background-color: #5569f2;border-bottom:1px solid #777">
        <span style="margin-right:10px;font-weight: 800">手术名称:</span>
        {{operationNames}}
      </div>
      <span style="display: inline-block;height:50px;line-height: 50px;">最新留言</span>
      <div class="newMessage">
        <ul class="mui-table-view">
          <li v-if="readedList.length == 0" class="mui-table-view-cell mui-media">您还没有未读最新留言</li>
          <li v-else class="mui-table-view-cell mui-media" v-for="item in readedList" :key="item.id">
            <router-link :to="{path:'/messageDetails',query:{id:item.id}}">
              <div class="mui-media-body" style="overflow:hidden;text-overflow: ellipsis;white-space: nowrap">
                <span class='mui-ellipsis' style="margin-right:10px;">{{ item.messageDate }}</span>
                <span style="">{{ item.message }}</span>
              </div>
            </router-link>
          </li>
        </ul>
      </div>
      <span style="display: inline-block;height:50px;line-height: 50px;">历史留言</span>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell mui-media" v-for="item in list" :key="item.id">
          <router-link :to="{path:'/messageDetails',query:{id:item.id}}">
            <div class="mui-media-body" style="overflow:hidden;text-overflow: ellipsis;white-space: nowrap">
              <span class='mui-ellipsis' style="margin-right:10px;">{{ item.messageDate }}</span>
              <span style="">{{ item.message }}</span>
            </div>
          </router-link>
        </li>
      </ul>
      <!--<ul>
        <li class="opeLi" v-for="item in list" :key="item.id">
          <span class="opeMessage">{{ item.message }}</span>
          <br/>
          <span class="opeData">{{ item.messageDate }}</span>
        </li>
      </ul>-->
    </div>
  </div>
</template>
<script>
  var p = 10;
  export default {
    data:function(){
      return {
        operationNames:'',
        list:[],//未读消息
        readedList:[],//已读消息
        allLoaded:false
      }
    },
    created:function(){
      this.getList();
    },
    mounted:function(){

    },
    methods:{
      loadTop: function () {  // 刷新数据的操作
      var self = this;
      setTimeout(function () {
        self.list.splice(0, self.list.length);
        var i =0, len=20;
        for (; i< len; i++){
          self.list.push('demo' + i);
        }
        self.$refs.loadmore.onTopLoaded();
      }, 2000);
    },
      loadBottom: function(){
        this.$refs.loadmore.onBottomLoaded();
        this.getList();
      },
      getList: function(){
        var url = '/api/com/wap/getLeaveMessageList';
        this.$http.post(url).then(function(res){
          if(res.body.zt == 'true'){
            //console.log(res);
            this.list = res.body.data.leaveMessage;
            this.readedList = res.body.data.readMessage;
            this.operationNames = res.body.data.operationName;
          }
        });
      }
    }
  }
</script>

<style scoped>
  .newMessage{
    max-height:258px;
    overflow: auto
  }
  .opeLi{
    clear: both;
    height:60px;
  }
  .opeMessage{
    color:#26A2FF;
  }
  .opeData{
    float:right;
    margin-right:20px;
  }
  #tmpl{
    overflow: scroll;
  }
  .item .name{
    height: 40px;
    margin-bottom: 10px;
    box-sizing: border-box;
    padding-left:20px;
    padding-top:10px;
  }
  .mui-ellipsis{
    color:#ccc;
  }
</style>



