<!--意见反馈页面-->
<template>
  <div id="tmpl">
    <div class="messageTitle">
      <p>当您对网站服务不满意时，请在此处联系我们</p>
      <p>注：您提交的问题不会展示给医生</p>
    </div>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <textarea v-model="suggesstionMsg" class="weui-textarea" placeholder="请输入反馈意见" rows="4"></textarea>        </div>
      </div>
    </div>
    <span @click="postSuggesstion" class="weui-btn weui-btn_primary">提交</span>
    <div class="messageTitle">
      <h4 class="toast">温馨提醒</h4>
      <span>您的意见对我们至关重要，我们懂得聆听，知错就改，我们会在收到您的反馈后第一时间联系您，请耐心等待。</span>
    </div>
  </div>
</template>
<script>
  import {Toast} from 'mint-ui';
  export default {
    data:function(){
      return {
        suggesstionMsg:''
      }
    },
    created:function(){

    },
    methods:{
      //提交反馈意见
      postSuggesstion:function(){
        var url = '/api/com/wap/saveFeeBack';
        this.$http.post(url,{
          message:this.suggesstionMsg
        },{emulateJSON:true}).then(function(res){
          if(res.body.zt == 'true'){
            Toast('您的反馈意见提交成功');
            this.suggesstionMsg = '';
          }else{
            Toast('您的反馈意见提交失败');
            this.suggesstionMsg = '';
          }
        });
      }
    }
  }
</script>
<style scoped>
  .toast{
    color:#20C5FC;
  }
  .weui-btn{
    margin-top:10px;
    margin-bottom:20px;
    width:96%;
  }
.messageTitle{
  box-sizing: border-box;
  padding:20px 10px 10px 10px;
}
</style>



