<template>
  <div id="tmpl">
    <div class="detailsMain">
      <h4 class="pageTitle">评论详情</h4>
      <span class="times">评论时间:</span><span>{{ detailsList.messageDate }}</span>
      <br/><br/>
      <span>评论内容:</span><br/>
      <span class="contents">
        {{ detailsList.message }}
      </span>
      <div class="btnDiv">
        <Button class="btns" type="primary">同意</Button>
        <Button class="btns" type="error">拒绝</Button>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data:function(){
      return {
        opeId:'',
        detailsList:[]
      }
    },
    beforeMount:function(){
      this.opeId = this.$route.query.id;
      this.getMessageDetails();
    },
    created:function(){

    },
    methods:{
      getMessageDetails:function(){
        var url = '/api/com/wap/getMessageById';
        this.$http.post(url,{
          id:this.opeId
        },{emulateJSON:true}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            this.detailsList = res.body.data;
          }
        });
      }
    }
  }
</script>
<style scoped>
  .btnDiv{
    text-align: center;
  }
  .btnDiv .btns{
    margin:10px;
  }
  .contents{
    display: inline-block;
    box-sizing: border-box;
    padding:20px;
    height: 350px;
    overflow: auto;
  }
  .times{
    margin-right:20px;
  }
  .pageTitle{
    text-align: center;
  }
  .detailsMain{
    box-sizing: border-box;
    padding:10px;
  }
</style>



