<!-- 出院指导详细页面（文档展示） -->

<template>
  <div id="tmpl">
    <iframe style="width:100%;height:100%" :src="files.fileUrl" frameborder="1px solid #ccc"></iframe>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        fileId:0,
        files:[]
      }
    },
    created:function(){
      this.getId();
      this.getFiles();
    },
    methods:{
      getId:function(){
        this.fileId = this.$route.query.fileId;
        //console.log(this.fileId);
      },
      getFiles:function(){
        var url = '/api/com/wap/getFileById';
        this.$http.get(url,{params:{fileId:this.fileId}}).then(function(res){

          if(res.body.zt === 'true'){
            this.files = res.body.data;
            console.log(res);
          }
        });
      }
    }
  }
</script>

<style scoped>
  img{
    width:10%;
  }
/*  ul{
    list-style: none;
  }
  li{
