  li a{
    display: inline-block;
    width:100%;
    height:50px;
    line-height: 50px;
  }
  li a span:last-child{
    display: inline-block;
    width:60%;
    height:30px;
    line-height: 30px;
  }
  .imgParent{
    display: inline-block;
    width:50px;
    height:50px;
  }
  .imgParent img{
    width:100%;
  }*/
</style>



