// 1.0 导入vue核心包
import Vue from 'vue';

// 2.0 导入App.vue的vue对象
import App from './App.vue';

// 3.0 将vue-router集成到这个项目中来
import vueRouter from 'vue-router';
// 3.0.1 将vueRouter对象绑定到Vue对象上
Vue.use(vueRouter);

//使用iviewUI组件库
import iView from 'iview'; // 导入组件库
import 'iview/dist/styles/iview.css'; // 导入样式
Vue.use(iView);

//引入weUI组件库
import 'weui/dist/style/weui.min.css';

//引入请求数据的插件vue-resource
import vueResource from 'vue-resource'
Vue.use(vueResource);

// 3.0.2 导入路由规则对应的组件对象
import index from './components/index.vue';
import SurgicalArrangement from './components/account/SurgicalArrangement.vue';
import returnVisit from './components/account/returnVisit.vue';
import opePlan from './components/account/opePlan.vue';
import opeEducation from './components/account/opeEducation.vue';
import opeEducationDetails from './components/account/opeEducationDetails.vue';
import outHospitalEducation from './components/account/outHospitalEducation.vue';
import outHospitalEducationDetails from './components/account/outHospitalEducationDetails.vue';
import outHospitalAppraise from './components/account/outHospitalAppraise.vue';
import LeavingMessage from './components/account/LeavingMessage.vue';
import personalPage from './components/account/personalPage.vue';
import personalInfoPage from './components/account/personalInfoPage.vue';
import suggestion from './components/account/suggestion.vue';
import messageDetails from './components/account/messageDetails.vue';

// 3.0.2 定义路由规则
var router1 = new vueRouter({
	routes:[
		{path:'/',component:index,meta:{title:'日间手术管理系统'}},
		{path:'/SurgicalArrangement',component:SurgicalArrangement,meta:{title:'手术排程'}},
		{path:'/returnVisit',component:returnVisit,meta:{title:'复诊'}},
		{path:'/opePlan',component:opePlan,meta:{title:'术日安排'}},
		{path:'/opeEducation',component:opeEducation,meta:{title:'术前宣教'}},
		{path:'/opeEducationDetails',component:opeEducationDetails,meta:{title:'术前宣教详情'}},
		{path:'/outHospitalEducation',component:outHospitalEducation,meta:{title:'出院指导'}},
		{path:'/outHospitalEducationDetails',component:outHospitalEducationDetails,meta:{title:'出院指导详情'}},
		{path:'/outHospitalAppraise',component:outHospitalAppraise,meta:{title:'离院评估'}},
		{path:'/LeavingMessage',component:LeavingMessage,meta:{title:'留言'}},
		{path:'/personalPage',component:personalPage,meta:{title:'个人中心'}},
		{path:'/personalInfoPage',component:personalInfoPage,meta:{title:'个人资料'}},
		{path:'/suggestion',component:suggestion,meta:{title:'意见反馈'}},
		{path:'/messageDetails',component:messageDetails,meta:{title:'评论详情'}}
	]
	});
router1.beforeEach(function(to,from,next){
  if(to.meta.title){
    document.title = to.meta.title
  }else{
    document.title = 'personal learn'
  }
  next()
});
export default router1;
// 4.0 注册mint-ui
// 导入mint-ui的css文件
import 'mint-ui/lib/style.min.css';
// 导入mint-ui组件对象
import mintui from 'mint-ui';
// 在Vue中全局使用mintui
Vue.use(mintui);

// 5.0 注册mui的css样式
import '../static/mui/css/mui.css';

// 5.0 利用Vue对象进行解析渲染
new Vue({
	el:'#app',
	// 使用路由对象实例
	router:router1,
	// render:function(create){create(App)} //es5的写法
	render:c=>c(App)  // es6的函数写法 =>：goes to
});



