<!-- 术前宣教 点击文件列表，详情展示（播放视频，文件预览） -->
<template>
  <div id="tmpl">
    <video id="videoGo" controls preload="auto" poster="../../../static/imgs/JHD.jpg" :style="videoStyle" :src="files.fileUrl"></video>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        videoStyle:{
          width:'100%',
          height:'100%'
        },
        fileId:0,
        files:[]
      }
    },
    created:function(){
      this.fileId = this.$route.query.fileId;
      //console.log(this.fileId);
      this.getFiles();
    },
    methods:{
      getFiles:function(){
        var url = '/api/com/wap/getFileById';
        this.$http.get(url,{params:{fileId:this.fileId}}).then(function(res){
          if(res.body.zt === 'true'){
            this.files = res.body.data;
            console.log(res);
          }
        });
      }
    }
  }
</script>

<style scoped>


</style>



