<template>
  <div id="tmpl">
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label">姓名</label>
        </div>
        <div class="weui-cell__bd">
          <input v-model="changeUserName" class="weui-input" placeholder="点击修改姓名"/>
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label">手机号</label>
        </div>
        <div class="weui-cell__bd">
          <input v-model="tel" class="weui-input" placeholder="点击修改手机号"/>
        </div>
      </div>
    </div>
    <!--<div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd"></div>
        <div class="weui-cell__bd">
          <p>微信昵称</p>
        </div>
        <div class="weui-cell__ft">{{ UserInfoAll.reallyName }}</div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"></div>
        <div class="weui-cell__bd">
          <p>手机号</p>
        </div>
        <div class="weui-cell__ft">{{ UserInfoAll.tel }}</div>
      </div>
    </div>-->
    <span @click="submitInfo" class="weui-btn weui-btn_plain-primary">提交认证</span>
  </div>
</template>
<script>
  import {Toast} from 'mint-ui';
  export default {
    data:function(){
      return {
        //用户手机号
        tel:'',
        //用户名
        changeUserName:''
        //userNames:'',
        //UserInfoAll:[]
      }
    },
    created:function(){
      //this.userNames = this.$route.query.userName;
      this.getUserInfoAll();
    },
    methods:{
      getUserInfoAll:function(){
        var url = '/api/com/wap/getUserBasicInfo';
        this.$http.post(url).then(function(res){
          console.log(res);
          if(res.body.zt == 'true'){
            this.tel = res.body.data.tel ;
            this.changeUserName = res.body.data.reallyName;
          }
        });
      },
      //提交个人认证信息
      submitInfo:function(){
        var url = '/api/com/wap/saveUserBasicInfo';
        //验证微信名
        //验证手机号
        var telReg = /^[1][0-9]{10}$/;
        if(this.changeUserName == ''){
          Toast('微信名不能为空');
        }else if(!telReg.test(this.tel)){
          Toast('请输入正确的手机号');
        }else{
          this.$http.post(url,{
            reallyName:this.changeUserName,
            tel:this.tel
          },{emulateJSON:true}).then(function(res){
            console.log(res);
            if(res.body.zt == 'true'){
              Toast('认证提交成功');
            }
          });
        }
      }
    }
  }
</script>
<style scoped>
.weui-btn_plain-primary{
  margin-top:10px;
  width:90%;
}
</style>



