<!-- 手术排程 -->

<template>
  <div id="tmpl">
    <div class="titles">
      <h5>
        <span>{{ data.patientName }}</span>
        <span>，您好</span>
      </h5>
      <p>您的手术【{{ data.operationName }}】安排在{{ data.operationDate }}上午{{ data.startDate }}，请于当日8:00前到院</p>
      <div class="btnGroup">
        <mt-button size="small" type="primary">取消预约</mt-button>
        <mt-button size="small" type="primary">取消手术</mt-button>
        <mt-button size="small" type="primary">同意</mt-button>
      </div>
    </div>
   <!-- <div class="content">
      &lt;!&ndash;<div>
        <span>手术当日安排</span>
      </div>
      <ul>
        <li>{{ data.operationPlan }}</li>
      </ul>&ndash;&gt;
      &lt;!&ndash;<ul>
        <li>
          <span>1、8:00到院</span>
        </li>
        <li>
          <span>2、携带全部检查结果</span>
        </li>
        <li>
          <span>3、医保病人携带社保卡</span>
        </li>
        <li>
          <span>4、携带病历本</span>
        </li>
        <li>
          <span>5、携带押金8000元，多退少补，可刷卡</span>
        </li>
        <li>
          <span>6、门诊楼2层办理住院手续</span>
        </li>
        <li>
          <span>7、门诊楼9层眼科手术室做术前准备（谈话、签字）</span>
        </li>
      </ul>&ndash;&gt;
    </div>-->
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        data:[]
      }
    },
    created:function(){
      this.getData();
    },
    methods:{
      getData:function(){
        var url = '/api/com/wap/getOperationManager';
        this.$http.post(url).then(function(res){
          console.log(res);
          if(res.body.zt === 'true'){
            this.data = res.body.data
          }
        });
      }
    }
  }
</script>

<style scoped>
.titles{
  padding:10px;
  width:100%;
  /*border-bottom:1px solid #ccc;*/
}
.btnGroup{
  box-sizing:border-box;
  padding:0 10%;
}
.btnGroup button{
  box-sizing:border-box;
  padding:6px 15px;
}
.content>div{
  margin-top:10px;
  width:100%;
  height:30px;
  box-sizing: border-box;
  padding:6px;
}
ul{
  padding:4px 20px;
  margin:0;
  list-style: none;
}
</style>



