<!--离院评估-->
<template>
  <div id="tmpl">
     <div class="outHospitalAppraise">
       <h3>
          尊敬的&nbsp;
         <span class="evaluate">{{ dataList.name }}</span>
       </h3>
       <br/>
       <span>
          您的评估状态为&nbsp;
         <span class="evaluate">{{ dataList.evaluate }}</span>
       </span>
       <br/><br/>
       <span v-if="dataList.evaluate == '允许离院'">
         请于&nbsp;
         <span class="evaluateTime"></span>
         &nbsp;离院
       </span>
       <span v-else-if="dataList.evaluate == '未评估'">
         未评估，请不要离院！
       </span>
       <span v-else="dataList.evaluate == '不允许离院'"></span>
     </div>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        dataList:[]
      }
    },
    created:function(){
      this.getData();
    },
    methods:{
      getData:function(){
        var url = '/api/com/wap/getOperationEvaluateSate';
        this.$http.get(url).then(function(res){
          //console.log(res);
          if(res.body.zt === 'true'){
            this.dataList = res.body.data;
            if(res.body.data.evaluate == 0){
              this.dataList.evaluate = '未评估';
            }else if(res.body.data.evaluate == 1){
              this.dataList.evaluate = '允许离院';
            }else{
              this.dataList.evaluate = '不允许离院';
            }
            //console.log(this.dataList);
          }
        });
      }
    }
  }
</script>

<style scoped>
  .outHospitalAppraise{
    width:100%;
    box-sizing: border-box;
    padding:10px;
  }
  .evaluate{
    color:#0062cc;
  }
</style>



